div.agreement {
    width: 80%;
    box-shadow: 4px 4px 20px var(--clr-light);
    border-radius: 1rem;
    padding: 1rem;
    display: flex;
}

div.agreement div.agreement-summary {
    flex: 1;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-right: 1rem;
}

div.agreement div.agreement-summary p {
    line-height: 1.5rem;
    font-size: 0.8rem;
}

div.agreement div.values p {
    font-weight: bold;
}

div.agreement div.labels {
    text-align: right;
}

div.agreement div.agreement-status-actions {
    flex: 0.6;
    padding: 0 0 0 1rem;
    border-left: 1px solid var(--clr-light);
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 2rem;
}

div.agreement div.status {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.9rem;
}

div.agreement div.status>p:last-child {
    font-weight: bold;
}

div.agreement div.action {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 1110px) {
    div.agreement {
        flex-direction: column;
        gap: 1rem;
    }

    div.agreement div.agreement-status-actions {
        padding: 0;
        padding-top: 1rem;
        border-left: 0;
        border-top: solid 1px var(--clr-light);
    }
    div.agreement div.agreement-status-actions div.status {
        justify-content: space-around;
    }
    div.agreement div.agreement-summary {
        padding: 0;
    }
    div.agreement div.agreement-summary div.labels {
        display: none;
    }
    div.agreement div.agreement-summary div.values {
        text-align: center;
    }
}