button.simple {
    padding: 1rem 1rem;
    width: 8rem;
    text-align: center;
    border: none;
    font-weight: bold;
    font-size: 1rem;
    color: white;
    border-radius: 1rem;
    box-shadow: 2px 2px 2px var(--clr-light);
}

button.simple:hover {
    cursor: pointer;
}

@media screen and (max-width: 400px) {
    button.simple {
        width: 5rem;
       font-size: 0.8rem;
    }
}